.SearchArea {
    width: 50vw;
    margin: 2rem auto;
    border-radius: 1rem;
    border-bottom: 1px solid rgba(0, 0, 0, 0.096);
}

.SearchForm {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.SearchForm input {
    font-size: 1rem;
    margin: 1rem;
    width: 60%;
    padding: 0.75rem;
    border: 0;
    border-bottom: 2px solid #29B9A8;
    border-radius: 2rem;
    outline: none;
}

.SearchForm button {
    background: #5ACEA5;
    font-size: 1rem;
    text-transform: capitalize;
    width: 25%;
    padding: 0.75rem;
    cursor: pointer;
    border: 0;
    border-radius: 3rem;
    outline: none;
    margin-bottom: 1rem;
}

.SearchForm button:hover {
    background: #29B9A8;
}

@media only screen and (max-width: 700px) {
    .SearchForm input {
        width: 100%;
    }
    .SearchForm button {
        width: 50%;
    }
}